<template>
  <div class="Main">
    <!-- 面包屑 -->
    <el-breadcrumb class="Breadcrumb">
      <el-breadcrumb-item class="left">设备管理</el-breadcrumb-item>
      <el-breadcrumb-item class="right">新增设备</el-breadcrumb-item>
