            ref="form"
            label-position="left"
            :rules="rules"
          >
            <el-form-item
              label="设备名称: "
              :label-width="formLabelWidth"
              prop="name"
            >
              <el-input
                v-model="form.name"
                placeholder="请输入设备名称"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="设备品牌: "
              :label-width="formLabelWidth"
              prop="brand"
            >
              <el-input
                v-model="form.brand"
                autocomplete="off"
                placeholder="请输入设备品牌"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="设备型号/规格: "
              :label-width="formLabelWidth"
              prop="type"
            >
              <el-input
                v-model="form.type"
                autocomplete="off"
                placeholder="请输入设备型号/规格"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="设备编号: "
              :label-width="formLabelWidth"
              prop="deviceNo"
            >
              <el-input
                v-model="form.deviceNo"
                autocomplete="off"
                placeholder="请输入设备编号"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="是否为关键设备:"
              :label-width="formLabelWidth"
              prop="crux"
            >
              <el-radio-group v-model="form.crux">
                <el-radio label="Y">是</el-radio>
                <el-radio label="N">否</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item
              label="设备分类: "
              :label-width="formLabelWidth"
              prop="clazz"
            >
              <el-input
                v-model="form.clazz"
                autocomplete="off"
                placeholder="请输入设备分类"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="维护保养标准标号: "
              :label-width="formLabelWidth"
              prop="fixnumber"
            >
              <el-input
                v-model="form.fixnumber"
                autocomplete="off"
                placeholder="请输入维护保养标准标号"
              ></el-input>
            </el-form-item>
          </el-form>
          <div class="submitbtn">
            <el-button type="primary" @click="submitNewDevice(form)"
              >保存并提交</el-button
            >
          </div>
        </div>
      </div>
      <div class="ExtraInfo">
        <div class="ExtraInfoText">拓展信息</div>
        <div class="Btns">
          <el-button class="AddBtn" icon="el-icon-circle-plus-outline"
            >新增字段</el-button
          >
          <el-button class="DelBtn" icon="el-icon-delete">删除字段</el-button>
          <el-button class="MoreDelBtn" icon="el-icon-delete"
            >批量删除</el-button
          >
        </div>
        <div class="ExtraInfoTable"></div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      form: {},
      formLabelWidth: "150px",
      rules: {
        name: [
          {
            required: true,
            message: "必填字段不能为空！",
            trigger: "blur",
          },
        ],
        brand: [
          {
            required: true,
            message: "必填字段不能为空！",
            trigger: "blur",
          },
        ],
        type: [
          {
            required: true,
            message: "必填字段不能为空！",
            trigger: "blur",
          },
        ],
        deviceNo: [
          {
            required: true,
            message: "必填字段不能为空！",
            trigger: "blur",
          },
        ],
        crux: [
          {
            required: true,
            message: "必填字段不能为空！",
            trigger: "blur",
          },
        ],
        clazz: [
          {
            required: true,
            message: "必填字段不能为空！",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // 提交新增设备
    submitNewDevice(formData) {
      console.log(formData);
      if (
        formData.name == undefined ||
        formData.brand == undefined ||
        formData.type == undefined ||
        formData.deviceNo == undefined ||
        formData.crux == undefined ||
        formData.clazz == undefined
      ) {
        this.$alert("请将基本信息填写完整！", "提示", {
          confirmButtonText: "确定",
        });
      } else {
        let obj = {};
        if (formData.crux == "Y") {
          obj.crux = true;
        } else if (formData.crux == "N") {
          obj.crux = false;
        }
        axios
          .post("http://47.102.214.37:8080/device", {
            name: formData.name,
            brand: formData.brand,
            type: formData.type,
            deviceNo: formData.deviceNo,
            crux: obj.crux,
            clazz: formData.clazz,
          })
          .then((res) => {
            if (res.data.message == "ok") {
              this.$message({
                message: "添加成功",
                type: "success",
              });
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
  },
};
</script>
